.cart {
	button{
		border: none;
		background-color: inherit;
	}

  &__top {
    display: grid;
	 grid-template-columns: 1fr 1fr;
    justify-items: center;
    align-items: center;
	gap: 20px;
	 @media (max-width:450px) {
		grid-template-columns: 1fr 
	 }
  }

  .content__title {
    display: grid;
	 grid-template-columns: auto auto;
    align-items: center;
    font-size: 32px;
	margin-right: 20px;
	@media (max-width:450px) {
		font-size: 25px;
	
	}

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: grid;
	 grid-template-columns: 1fr auto;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {

      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
	 @media (max-width:450px) {
		
			display: grid;
			grid-template-columns: 1fr auto 1fr;
		
	 }
  }

  .content__items{
	display: block;
  }

  &__item {
    display: grid;
	 grid-template-columns: 80px 1fr auto 1fr;
	 align-items: center;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;
	 gap: 20px;
	 @media (max-width: 520px) {
		display: grid;
		grid-template-columns: 80px auto;
		
	 }
	
	 &-latest{
		display: grid;
		grid-template-columns: auto auto;
		gap: 20px;
		justify-items: flex-end;

		@media (max-width: 590px) {
			gap: 5px;
			
		 }
	 }

    &-img {
      display: grid;
      align-items: center;
      margin-right: 55px;
      width: 10%;
		

      img {
        width: 80px;
        height: 80px;
      }
    }

    &-info {
      @media (max-width: 520px) {
			display: grid;
			grid-template-columns: auto 1fr;
			gap: 5px;
		 }

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }

		  &--disabled{
			opacity: 0.4;
			border-color: #787878;

			&:active{
				transform: none;
			}

			&:hover{
				background-color: transparent;
			
				svg path {
					fill: #787878;
				}
			}

			svg path {
				fill: #787878;
			}
		  }
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
     

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 4%;

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        img {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: grid;
		grid-template-columns: 1fr 1fr;
      justify-content: space-between;

		@media (max-width: 510px) {
			grid-template-columns:  1fr;
			gap: 20px
		}

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;
		gap: 20px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
      }
    }
  }

  &--empty {
    margin: 50px auto;
    max-width: 560px;
    text-align: center;

    h2 {
      font-size: 32px;
		padding: 20px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}
